@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo&display=swap);
/* /////////////////////////// */
/* only global rules are added here */
/* /////////////////////////// */

* {
  font-family: 'Source Code Pro', 'Cairo', Helvetica, Arial, sans-serif;
  box-sizing: border-box;
}

body {
  height: 100%;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: auto;
}

#root {
  height: 100%;
}

.App {
  height: 100%;
}

body::-webkit-scrollbar-track
{
  /* -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); */
  background-color: #ff000000;
}

body::-webkit-scrollbar
{
  width: 10px;
  background-color: #00000013;
}

body::-webkit-scrollbar-thumb
{
  background-color: #79797975;
  /* border: 1px solid #7c7c7c; */
  border-radius: 10px;
}
.socialMedia {
    display: flex;
    flex-direction: column;
    position: absolute;
    z-index: 40;
    bottom: 0;
    left: 0;
    padding: 15px;
}

.socialMedia div {
    width: 30px;
    height: 30px;
    /* border-radius: 50%; */
    margin-top: 15px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    transition: 500ms;
    opacity: 0.5;
    cursor: pointer;
    -webkit-filter: invert(1);
            filter: invert(1);
    padding: 16px;
}

.socialMedia div:hover {
    opacity: 1;
}

.forCode {
    -webkit-filter: invert(1) !important;
            filter: invert(1) !important;
}

.forDesign {
    -webkit-filter: invert(0) !important;
            filter: invert(0) !important;
}

@media (max-aspect-ratio: 1/1) {
    .socialMedia div {
        -webkit-filter: invert(0);
                filter: invert(0);
    }
}
.Main {
    height: 100%;
    position: relative;
    overflow: hidden;
}

.bigContainer {
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(207, 207, 207);
}

body .part {
    position: inherit;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 10%;
    max-width: 125px;
    z-index: 10;
    cursor: pointer;
}

body div .text {
    margin-top: 10px;
    font-size: larger;
    text-align: center;
    font-weight: bold;
}

body .codeDiv {
    color: white;
}

.codeDesign {
    position: absolute;
    z-index: 10; 
    height: 100%;
    width: 60%;
    right: -60px;
    -webkit-animation-name: topToDown;
            animation-name: topToDown;
    -webkit-animation-duration: 80s;
            animation-duration: 80s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    opacity: 0%;
    object-fit: cover;
}

@-webkit-keyframes topToDown {
    0% {top: -50%;}
    50% {top: 50%;}
    100% {top: -50%;}
}

@keyframes topToDown {
    0% {top: -50%;}
    50% {top: 50%;}
    100% {top: -50%;}
}

.animationBackground {
    position: absolute;
    background-color: rgb(37, 37, 37);
    width: 100%;
    height: 100%;
    left: -50%;
    top: 0;
    z-index: 1;
    -webkit-transform: skew(-8deg, 0deg);
            transform: skew(-8deg, 0deg);
}

.bigBackground {
    position: absolute;
    background: linear-gradient(0deg, rgba(32,32,32,1) 0%, rgba(0,0,0,1) 100%);
    width: 100%;
    height: 100%;
    z-index: 5;
    opacity: 0%;
}

.codeLogoBorder, .designLogoBorder {
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.theDesignDesign{
    position: absolute;
    width: 100%;
    height: auto;
    -webkit-transform-origin: center;
            transform-origin: center;
    left: -50%;
    -webkit-animation-name: rotationForPaths;
            animation-name: rotationForPaths;
    -webkit-animation-duration: 40s;
            animation-duration: 40s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    opacity: 0%;
    object-fit: cover;
    z-index: 10;
}

.theCodeSvg {
    -webkit-filter: drop-shadow(0px 0px  0px rgba(255, 255, 255, 0));
            filter: drop-shadow(0px 0px  0px rgba(255, 255, 255, 0));
    transition: 2s;
}
.theCodeSvg:hover {
    -webkit-filter: drop-shadow(0px 0px 25px #5fffd7c7);
            filter: drop-shadow(0px 0px 25px #5fffd7c7);
}
.theDesignSvg {
    -webkit-filter: drop-shadow(0px 0px  0px rgba(0, 0, 0, 0));
            filter: drop-shadow(0px 0px  0px rgba(0, 0, 0, 0));
    transition: 2s;
}
.theDesignSvg:hover {
    -webkit-filter: drop-shadow(0px 0px 25px #ff46e6);
            filter: drop-shadow(0px 0px 25px #ff46e6);
}

@-webkit-keyframes rotationForPaths {
    0% {-webkit-transform: rotateZ(0deg);transform: rotateZ(0deg);}
    100% {-webkit-transform: rotateZ(360deg);transform: rotateZ(360deg);}
}

@keyframes rotationForPaths {
    0% {-webkit-transform: rotateZ(0deg);transform: rotateZ(0deg);}
    100% {-webkit-transform: rotateZ(360deg);transform: rotateZ(360deg);}
}

.logo {
    position: absolute;
    /* width: 50px; */
    height: 50px;
    top: 20px;
    left: 20px;
    z-index: 10;
}

.secondBigContainer {
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 10;
    justify-content: space-around;
    align-items: center;
    -webkit-perspective: 1000px;
            perspective: 1000px;
}

.codeDescription  {
    position: absolute;
    display: none;
    flex-direction: column;
    align-items: flex-end;
    -webkit-filter: blur(50px);
            filter: blur(50px);
    -webkit-transform: rotateY(-50deg);
            transform: rotateY(-50deg);
    -webkit-transform-origin: left;
            transform-origin: left;
    opacity: 0.0;
    right: 33%;
    top: 50%;
    padding: 0 7.5%;
    padding-left: 0;
    font-size: 75px;
}

.codeDescription .description {
    background: -webkit-linear-gradient(45deg,rgb(66, 66, 66), white);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    text-align: right;
}

.codeDescription .buttons {
    display: flex;
    margin-top: 15px;
    height: 50px;
    justify-content: flex-end;
}

.codeDescription .button {
    position: relative;
    -webkit-perspective: 1000px;
            perspective: 1000px;
    font-size: 20px;
}

.codeDescription .button svg {
    height: 100%;
    width: 100%;
    -webkit-transform-origin: left;
            transform-origin: left;
}

.designDescription  {
    position: absolute;
    display: none;
    flex-direction: column;
    align-items: flex-start;
    -webkit-filter: blur(50px);
            filter: blur(50px);
    -webkit-transform: rotateY(50deg);
            transform: rotateY(50deg);
    -webkit-transform-origin: right;
            transform-origin: right;
    opacity: 0.0;
    left: 33%;
    top: 50%;
    padding: 0 7.5%;
    padding-right: 0;
    font-size: 75px;
}

.designDescription .description {
    background: -webkit-linear-gradient(45deg, black, rgb(192, 192, 192));
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.designDescription .buttons {
    display: flex;
    margin-top: 30px;
    height: 50px;
    justify-content: flex-start;
}

.designDescription .button {
    position: relative;
    -webkit-perspective: 1000px;
            perspective: 1000px;
    font-size: 20px;
}

.designDescription .button .buttonText {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    margin-top: 0;
    font-size: 100%;
    align-items: center;
    justify-content: center;
    background: -webkit-linear-gradient(45deg, rgb(90, 90, 90), rgb(0, 0, 0));
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
}

.codeDescription .button .buttonText {
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    margin-top: 0;
    font-size: 100%;
    align-items: center;
    justify-content: center;
    background: -webkit-linear-gradient(45deg, rgb(77, 77, 77), rgb(255, 255, 255));
    -webkit-background-clip: text;
            background-clip: text;
    -webkit-text-fill-color: transparent;
    font-weight: bold;
}

.designDescription .button svg {
    height: 100%;
    width: 100%;
    -webkit-transform-origin: right;
            transform-origin: right;
}

.back_button_design {
    cursor: pointer;
    transition: 0.3s;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    -webkit-filter: drop-shadow(0px 0px  5px #f58ae700);
            filter: drop-shadow(0px 0px  5px #f58ae700);
}

.back_button_design:hover {
    -webkit-filter: drop-shadow(-5px 5px  5px #ff46e6);
            filter: drop-shadow(-5px 5px  5px #ff46e6);
}

.designButton {
    cursor: pointer;
    transition: 0.3s;
    -webkit-filter: drop-shadow(0px 0px 5px #f58ae700);
            filter: drop-shadow(0px 0px 5px #f58ae700);
}

.designButton:hover {
    -webkit-filter: drop-shadow(5px 5px  5px #ff46e6);
            filter: drop-shadow(5px 5px  5px #ff46e6);
}

.back_button_code {
    cursor: pointer;
    transition: 0.3s;
    -webkit-filter: drop-shadow(0px 0px 5px #5fffd700);
            filter: drop-shadow(0px 0px 5px #5fffd700);
}

.back_button_code:hover {
    -webkit-filter: drop-shadow(5px 5px 5px #5fffd7);
            filter: drop-shadow(5px 5px 5px #5fffd7);
}

.codeButton {
    cursor: pointer;
    transition: 0.3s;
    -webkit-filter: drop-shadow(0px 0px 5px #5fffd700);
            filter: drop-shadow(0px 0px 5px #5fffd700);
}

.codeButton:hover {
    -webkit-filter: drop-shadow(5px 5px 5px #5fffd7);
            filter: drop-shadow(5px 5px 5px #5fffd7);
}

.languageIcon {
    transition: 1000ms;
}

/* ///////////// */
/* Media Queries */
/* ///////////// */
@media (max-aspect-ratio: 1/1) {
    body .part {
        /* width: 75px; */
        width: 20%;
        max-width: 100px;
    }
    .bigContainer {
        flex-direction: column;
    }
    .animationBackground {
        left: 0;
        top: -50%;
        -webkit-transform: skew(0deg, -8deg);
                transform: skew(0deg, -8deg);
    }
    .codeDescription  {
        right: 50%;
        top: 66%;
        padding: 5%;
        width: 80%;
    }
    .designDescription  {
        left: 50%;
        top: 33%;
        padding: 5%;
        width: 80%;
    }
    .languageIcon {
        -webkit-filter: invert(1);
                filter: invert(1);
    }
}

@media screen and (max-width: 750px) {
    .codeDescription {
        font-size: 40px;
    }
    .codeDescription .buttons {
        height: 40px;
    }
    .designDescription {
        font-size: 40px;
    }
    .designDescription .buttons {
        height: 40px;
    }
    .logo {
        width: 40px;
    }
    .designDescription .button .buttonText {
        font-size: 80%;
    }
    .codeDescription .button .buttonText {
        font-size: 80%;
    }
}

@media screen and (max-width: 366px) {
    .codeDescription {
        font-size: 30px;
    }
    .codeDescription .buttons {
        height: 30px;
    }
    .designDescription {
        font-size: 30px;
    }
    .designDescription .buttons {
        height: 30px;
    }
    .logo {
        width: 30px;
    }
    .designDescription .button .buttonText {
        font-size: 65%;
    }
    .codeDescription .button .buttonText {
        font-size: 65%;
    }
}
.flex {
    display: flex;
    align-items: center;
}

a {
    color: white;
    cursor: pointer;
    text-decoration: none;
}

.pageBigContainer {
    background-color: #ffffff;
    background-image: linear-gradient(#2b2b2b, #000000e5);
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100%;
    overflow: auto;
    padding-bottom: 50px;
}

.pageHeader {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    z-index: 2;
    min-height: 350px;
    width: 100%;
    padding: 0 5%;
    background-position: center;
    background-size: cover;
    /* shaddow */ 
    box-shadow: 0px 0px 50px 0px rgba(0,0,0,0.5);
}

.pageHeaderContainer {
    max-width: 750px;
    width: 100%;
    /* height: 100%; */
    color: white;
    display: flex;
    flex-direction: column;
}

.pageBody {
    max-width: 750px;
    margin: 0 5%;
}

.pageBody h1 {
    color: white;
    margin: 60px 0 30px 0; 
}

.pageBody .description {
    position: relative;
    z-index: 1;
    padding: 30px;
    border-radius: 0 0 30px 30px;
    background-color: white;
    background-image: linear-gradient(#000000dc, #000000ce);
    color: white;
    /* shaddow */ 
    box-shadow: 0px 0px 50px 0px rgba(0,0,0,0.2);
    /* border */
    border: 3px solid rgba(255, 255, 255, 0.164);
    border-top: none;
}

.skillsPart {
    display: grid;
    grid-template-columns: auto auto auto auto auto ;
    grid-gap: 40px;
    gap: 40px;
}

.skillsPart div {
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    /* max-height: 120px;
    height: 15vw; */
    height: 100px;
}

.divWithBorder {
    border-left: 2.5px solid rgba(255, 255, 255, 0.5);
    padding: 0 20px;
    margin: 0 3.5px;
}

.circle {
    width: 10px;
    height: 10px;
    background-color: rgba(255, 255, 255, 0.5);
    border-radius: 50%;
    margin: 10px 0;
}

.card {
    width: 100%;
    padding-left: 25px;
    padding-right: 0;
}

.innerCard {
    position: relative;
    background-color: white;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 300px;
    border-radius: 15px; 
    box-shadow: 0px 0px 50px 0px rgba(0,0,0,0.5);
    padding: 20px;
}

.innerCard:hover .whenHovering {
    opacity: 1;
}

.cardTitle {
    font-size: xx-large;
    color: white;
    font-weight: bold;
    margin-bottom: 10px;
    text-shadow: 2px 2px 10px black, 2px 2px 10px black, 2px 2px 10px black;
}

.cardDescription {
    color: white;
    text-shadow: 2px 2px 10px black, 2px 2px 10px black, 2px 2px 10px black;
    font-weight: bold;
}

.whenHovering {
    display: flex;
    opacity: 0.0;
    position: absolute;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.452);
    transition: 0.25s;
    border-radius: 15px;
}

.whenHovering img {
    width: 75px;
}

@media only screen and (max-width: 500px) {
    .skillsPart {
        grid-template-columns: auto auto auto;
    }
}
